<template>
  <div>
    <el-breadcrumb separator-icon="ArrowRight" style="margin: 16px">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>dormitory management</el-breadcrumb-item>
      <el-breadcrumb-item>room info</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card style="margin: 15px; min-height: calc(100vh - 111px)">
      <div>
        <!--    功能区-->
        <div style="margin: 10px 0">
          <!--    搜索区-->
          <div style="margin: 10px 0">
            <el-input v-model="search" clearable placeholder="Please enter the room number" prefix-icon="Search" style="width: 20%"/>
            <el-button icon="Search" style="margin-left: 5px" type="primary" @click="load"></el-button>
            <el-button icon="refresh-left" style="margin-left: 10px" type="default" @click="reset"></el-button>
            <div style="float: right">
              <el-tooltip content="添加" placement="top">
                <el-button icon="plus" style="width: 50px" type="primary" @click="add"></el-button>
              </el-tooltip>
            </div>
          </div>
        </div>
        <!--    表格-->
        <el-table v-loading="loading" :data="tableData" border max-height="705" style="width: 100%">
          <el-table-column label="#" type="index"/>
          <!-- 床位展开-->
          <el-table-column type="expand">
            <template #default="props">
              <el-form inline label-position="left">
                <el-form-item label="Bed No.1" class="item">
                  <template #default="scope">
                    <el-tag v-if="props.row.firstBed != null" disable-transitions type="primary"
                    >{{ props.row.firstBed }}
                    </el-tag>
                    <div class="el-form--inline-icon">
                      <el-icon v-if="props.row.firstBed == null" @click="plusIcon(1, props.row)">
                        <plus/>
                      </el-icon>
                      <div v-if="props.row.firstBed != null" class="el-form--inline-icon">
                        <el-icon @click="detailIcon(1, props.row)">
                          <more-filled/>
                        </el-icon>
                        <el-icon @click="editIcon(1, props.row)">
                          <edit/>
                        </el-icon>
                        <el-popconfirm title="confirm delete？" @confirm="deleteStuBed(1, props.row)">
                          <template #reference>
                            <el-icon>
                              <delete/>
                            </el-icon>
                          </template>
                        </el-popconfirm>
                      </div>
                    </div>
                  </template>
                </el-form-item>
                <el-form-item label="Bed No.2" class="item">
                  <template #default="scope">
                    <el-tag v-if="props.row.secondBed != null" disable-transitions type="primary"
                    >{{ props.row.secondBed }}
                    </el-tag>
                    <div class="el-form--inline-icon">
                      <el-icon v-if="props.row.secondBed == null" @click="plusIcon(2, props.row)">
                        <plus/>
                      </el-icon>
                      <div v-if="props.row.secondBed != null" class="el-form--inline-icon">
                        <el-icon @click="detailIcon(2, props.row)">
                          <more-filled/>
                        </el-icon>
                        <el-icon @click="editIcon(2, props.row)">
                          <edit/>
                        </el-icon>
                        <el-popconfirm title="confirm delete？" @confirm="deleteStuBed(2, props.row)">
                          <template #reference>
                            <el-icon>
                              <delete/>
                            </el-icon>
                          </template>
                        </el-popconfirm>
                      </div>
                    </div>
                  </template>
                </el-form-item>
                <el-form-item label="Bed No.3" class="item">
                  <template #default="scope">
                    <el-tag v-if="props.row.thirdBed != null" disable-transitions type="primary"
                    >{{ props.row.thirdBed }}
                    </el-tag>
                    <div class="el-form--inline-icon">
                      <el-icon v-if="props.row.thirdBed == null" @click="plusIcon(3, props.row)">
                        <plus/>
                      </el-icon>
                      <div v-if="props.row.thirdBed != null" class="el-form--inline-icon">
                        <el-icon @click="detailIcon(3, props.row)">
                          <more-filled/>
                        </el-icon>
                        <el-icon @click="editIcon(3, props.row)">
                          <edit/>
                        </el-icon>
                        <el-popconfirm title="confirm delete？" @confirm="deleteStuBed(3, props.row)">
                          <template #reference>
                            <el-icon>
                              <delete/>
                            </el-icon>
                          </template>
                        </el-popconfirm>
                      </div>
                    </div>
                  </template>
                </el-form-item>
                <el-form-item label="Bed No.4" class="item">
                  <template #default="scope">
                    <el-tag v-if="props.row.fourthBed != null" disable-transitions type="primary"
                    >{{ props.row.fourthBed }}
                    </el-tag>
                    <div class="el-form--inline-icon">
                      <el-icon v-if="props.row.fourthBed == null" @click="plusIcon(4, props.row)">
                        <plus/>
                      </el-icon>
                      <div v-if="props.row.fourthBed != null" class="el-form--inline-icon">
                        <el-icon @click="detailIcon(4, props.row)">
                          <more-filled/>
                        </el-icon>
                        <el-icon @click="editIcon(4, props.row)">
                          <edit/>
                        </el-icon>
                        <el-popconfirm title="confirm delete？" @confirm="deleteStuBed(4, props.row)">
                          <template #reference>
                            <el-icon>
                              <delete/>
                            </el-icon>
                          </template>
                        </el-popconfirm>
                      </div>
                    </div>
                  </template>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column label="dormRoomId" prop="dormRoomId" sortable/>
          <el-table-column label="dormBuildId" prop="dormBuildId" sortable/>
          <el-table-column label="floorNum" prop="floorNum" sortable/>
          <el-table-column label="maxCapacity" prop="maxCapacity"/>
          <el-table-column
              :filter-method="filterTag"
              :filters="[
              { text: 0, value: 0 },
              { text: 1, value: 1 },
              { text: 2, value: 2 },
              { text: 3, value: 3 },
              { text: 4, value: 4 },
            ]"
              filter-placement="bottom-end"
              label="Number of residents"
              prop="currentCapacity"
              sortable
          />
          <!--      操作栏-->
          <el-table-column label="operation" width="130px">
            <template #default="scope">
              <el-button icon="Edit" type="primary" @click="handleEdit(scope.row)"
              ></el-button>
              <el-popconfirm title="confirm delete？" @confirm="handleDelete(scope.row.dormRoomId)">
                <template #reference>
                  <el-button icon="Delete" type="danger"></el-button>
                </template>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
        <!--分页-->
        <div style="margin: 10px 0">
          <el-pagination
              v-model:currentPage="currentPage"
              :page-size="pageSize"
              :page-sizes="[10, 20]"
              :total="total"
              layout="total, sizes, prev, pager, next, jumper"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
          >
          </el-pagination>
        </div>
        <!--      弹窗-->
        <div>
          <el-dialog v-model="dialogVisible" title="operation" width="30%" @close="cancel">
            <el-form ref="form" :model="form" :rules="rules" label-width="120px">
              <el-form-item label="dormBuildId" prop="dormBuildId">
                <el-input v-model.number="form.dormBuildId" style="width: 80%"></el-input>
              </el-form-item>
              <el-form-item label="floorNum" prop="floorNum">
                <el-input v-model.number="form.floorNum" style="width: 80%"></el-input>
              </el-form-item>
              <el-form-item label="dormRoomId" prop="dormRoomId">
                <el-input v-model.number="form.dormRoomId" :disabled="disabled" style="width: 80%"></el-input>
              </el-form-item>
              <el-form-item label="maxCapacity" prop="maxCapacity">
                <el-input v-model.number="form.maxCapacity" style="width: 80%"></el-input>
              </el-form-item>
              <el-form-item label="currentCapacity" prop="currentCapacity">
                <el-input v-model.number="form.currentCapacity" style="width: 80%"></el-input>
              </el-form-item>
            </el-form>
            <template #footer>
              <span class="dialog-footer">
                <el-button @click="cancel">cancel</el-button>
                <el-button type="primary" @click="save">confirm</el-button>
              </span>
            </template>
          </el-dialog>
          <!-- 床位 弹窗-->
          <el-dialog v-model="bedDialog" title="operation" width="30%" @close="cancel">
            <el-form ref="form" :model="form" :rules="rules" label-width="120px">
              <el-form-item label="dormBuildId" prop="dormBuildId">
                <el-input v-model.number="form.dormBuildId" disabled="true" style="width: 80%"></el-input>
              </el-form-item>
              <el-form-item label="floorNum" prop="floorNum">
                <el-input v-model.number="form.floorNum" disabled="true" style="width: 80%"></el-input>
              </el-form-item>
              <el-form-item label="dormRoomId" prop="dormRoomId">
                <el-input v-model.number="form.dormRoomId" disabled="true" style="width: 80%"></el-input>
              </el-form-item>
              <el-form-item v-if="this.bedNum === 1" label="bed(一)" prop="firstBed">
                <el-input v-model.number="form.firstBed" placeholder="Please enter your student ID" style="width: 80%"></el-input>
              </el-form-item>
              <el-form-item v-if="this.bedNum === 2" label="bed(二)" prop="secondBed">
                <el-input v-model.number="form.secondBed" placeholder="Please enter your student ID" style="width: 80%"></el-input>
              </el-form-item>
              <el-form-item v-if="this.bedNum === 3" label="bed(三)" prop="thirdBed">
                <el-input v-model.number="form.thirdBed" placeholder="Please enter your student ID" style="width: 80%"></el-input>
              </el-form-item>
              <el-form-item v-if="this.bedNum === 4" label="bed(四)" prop="fourthBed">
                <el-input v-model.number="form.fourthBed" placeholder="Please enter your student ID" style="width: 80%"></el-input>
              </el-form-item>
            </el-form>
            <template #footer>
              <span class="dialog-footer">
                <el-button @click="cancel">cancel</el-button>
                <el-button v-if="this.judge === false" type="primary" @click="addStuBed">confirm</el-button>
                <el-button v-if="this.judge === true" type="primary" @click="editStuBed">confirm</el-button>
              </span>
            </template>
          </el-dialog>
          <!-- 学生信息弹窗-->
          <el-dialog v-model="stuInfoDialog" title="studentinfo" width="20%" @close="cancel">
            <el-form ref="form" :model="form" label-width="120px">
              <el-form-item label="username：" prop="username">
                <template #default="scope">
                  <span>{{ form.username }}</span>
                </template>
              </el-form-item>
              <el-form-item label="name：" prop="name">
                <template #default="scope">
                  <span>{{ form.name }}</span>
                </template>
              </el-form-item>
              <el-form-item label="age：" prop="age">
                <template #default="scope">
                  <span>{{ form.age }}</span>
                </template>
              </el-form-item>
              <el-form-item label="gender：" prop="gender">
                <template #default="scope">
                  <span>{{ form.gender }}</span>
                </template>
              </el-form-item>
              <el-form-item label="phoneNum：" prop="phoneNum">
                <template #default="scope">
                  <span>{{ form.phoneNum }}</span>
                </template>
              </el-form-item>
              <el-form-item label="email：" prop="email">
                <template #default="scope">
                  <span>{{ form.email }}</span>
                </template>
              </el-form-item>
            </el-form>
          </el-dialog>
        </div>
      </div>
    </el-card>
  </div>
</template>
<script src="@/assets/js/RoomInfo.js"></script>
<style scoped>@import '../assets/css/RoomInfo.css';</style>